import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Slides, Slide, ToastController, LoadingController } from 'ionic-angular';
import { ImageViewerController } from 'ionic-img-viewer';
import { Observable } from 'rxjs';
import { AngularFirestore } from 'angularfire2/firestore';
import { Collections, Note } from '../../app/app.models';
import { AuthProvider } from '../../providers/auth/auth';

@IonicPage({ segment: "presentacion/:id" })
@Component({
  selector: "page-slide-detail",
  templateUrl: "slide-detail.html"
})
export class SlideDetailPage {
  public slide$: Observable<Slide>;
  public notas$: Observable<Note[]>;
  private id: string = this.navParams.get("id");

  public lc: boolean = false;
  public newNota: any = { title: "", text: "" };

  @ViewChild(Slides) slides: Slides;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore,
    private imgCtrl: ImageViewerController,
    private toast: ToastController,
    private auth: AuthProvider,
    private load: LoadingController
  ) {}

  ionViewDidLoad() {
    this.slide$ = this.afs
      .collection(Collections.SLIDE)
      .doc<Slide>(this.id)
      .valueChanges();

    this.auth.user$.subscribe(user => {
      if (user && !this.notas$) {
        this.notas$ = this.afs
          .collection<Note>(Collections.NOTE, ref =>
            ref.where("parent_id", "==", this.id).where("user", "==", user.uid)
          )
          .valueChanges();
      }
    });
  }

  openSlide(img) {
    this.imgCtrl.create(img).present();
  }

  async addNota(title: string, text: string) {
    if (!title)
      return this.toast
        .create({ message: "Ingresa un titulo", duration: 2000 })
        .present();
    if (!text)
      return this.toast
        .create({ message: "Ingresa un text", duration: 2000 })
        .present();

    const l = this.load.create({ content: "Agregando..." });
    l.present();

    const id = this.afs.createId();
    await this.afs
      .collection(Collections.NOTE)
      .doc(id)
      .set({
        id,
        title,
        text,
        parent_id: this.id,
        user: this.auth.user.uid
      });

    l.dismiss();
    this.newNota = {title: '', text: ''};

  }

  onSlideChange() {
    //this.index = this.slides.getActiveIndex()
    //
  }
}



